import db from "@/firebase/init";
import axios from "axios";

export default {
    state: {
        api: `http://localhost:5000/joboxza/us-central1/app/pay`
    },
    mutations: {
        complete: (state, id) => {
            console.log("complete");
            db.collection('payments').doc(id).get().then(doc => {
                if(doc.data().inboundPayment === true) {
                    axios.post(state.api, {
                      body: doc.data()
                    });
                }
                else {
                    console.log("You have not made the payment for the job.");
                }
            });
        }
    },
    actions: {
        complete: (context, id) => {
            context.commit("complete", id);
        }
    }
};