import db from '@/firebase/init';
import firebase from "firebase/app";
import axios from "axios";
import router from "@/routes/routes";

export default {
    state: {
        // Local Environment: http://localhost:5000/joboxza/us-central1/app/notification
        // QA Environment: https://us-central1-joboxza.cloudfunctions.net/app/notification
        api: `http://us-central1-joboxza.cloudfunctions.net/app/notification`,
        // <Modal>
        header: null,
        body: null,
        footer: null,
        modal: false,
        //</Modal>
        loading: false,
        success: false,
        error: false,
        content: null
    },
    getters: {
        // <Modal>
        headerFP: state => state.header,
        bodyFP: state => state.body,
        footerFP: state => state.footer,
        modalFP: state => state.modal,
        //</Modal>
        loadingFP: state => state.loading,
        successFP: state => state.success,
        errorFP: state => state.error
    },
    mutations: {
        forgot: (state, payload) => {
            if(payload.email) {
                state.loading = true;
                firebase.auth().sendPasswordResetEmail(payload.email)
                .then(() => {
                    db.collection("ContentManagement").doc("Forgot Password Success Modal").get().then(doc => {
                        state.content = doc.data();
                        state.loading = false;
                        state.header = state.content.header;
                        state.body = state.content.body;
                        state.footer = state.content.footer;
                        state.modal = true;
                        state.success = true;
                    })
                    .catch(errCMS => {
                        state.loading = false;
                        state.header = "Email Sent!";
                        state.body = "Go ahead and check your inbox to reset your password.";
                        state.footer = "Got it"
                        state.modal = true;
                        state.success = true;
                        if(errCMS) {
                            var args = {
                                email: "tech@jobox.co.za",
                                issue: "In the collection (ContentManagent), Document id (Forgot Password Success Modal) may be missing", 
                                message: errCMS.message,
                            }
                            // Send error to tech support
                            axios.post(state.api, {
                                body: args
                            });
                        }
                    });
                })
                .catch(err => {
                    db.collection("ContentManagement").doc("Forgot Password Error Modal").get().then(doc => {
                        state.content = doc.data();
                        state.loading = false;
                        state.header = state.content.header;
                        state.body = err.message;
                        state.footer = state.content.footer;
                        state.modal = true;
                        state.error = true;
                    })
                    .catch(errCMS => {
                        state.loading = false;
                        state.header = "Oops";
                        state.body = err.message;
                        state.footer = "Got it";
                        state.modal = true;
                        state.error = true;
                        if(errCMS) {
                            var args = {
                                issue: "In the collection (ContentManagent), Document id (Forgot Password Error Modal) may be missing", 
                                message: errCMS.message,
                            }
                            // Send error to tech support
                            axios.post(state.api, {
                                body: args
                            });
                        }
                    });
                });
            }
            else {
                db.collection("ContentManagement").doc("Forgot Password Error Modal").get().then(doc => {
                    state.content = doc.data();
                    state.loading = false;
                    state.header = state.content.header;
                    state.body = "Your email was not entered.";
                    state.footer = state.content.footer;
                    state.modal = true;
                    state.error = true;
                })
                .catch(errCMS => {
                    state.loading = false;
                    state.header = "Oops!";
                    state.body = "Your email was not entered.";
                    state.footer = "Got it";
                    state.modal = true;
                    state.error = true;
                    if(errCMS) {
                        var args = {
                            issue: "In the collection (ContentManagent), Document id (Forgot Password Error Modal) may be missing", 
                            message: errCMS.message,
                        }
                        // Send error to tech support
                        axios.post(state.api, {
                            body: args
                        });
                    }
                });
            }
        },
        sent: state => {
            router.push({ name: 'Login'});
            state.success = false;
            state.modal = false;
        },
        modalHide: state => {
            state.error = false;
            state.modal = false;
            
        }
    },
    actions: {
        forgot: (context, payload) => {
            context.commit('forgot', payload);
        },
        sent: context => {
            context.commit('sent');
        },
        modalHide: context => {
            context.commit('modalHide');
        }
    }
};