import OutboundPayment from "./modules/payment/outbound/store";
import Login from "./modules/login/store";
import Forgot from './modules/forgot/store';
Vue.use(Vuex);

export const store = new Vuex.Store({
    modules: {
        InboundPayment,
        OutboundPayment,
        Login,
        Forgot
    }
});